              </template>
                     <template v-slot:item.supports_multiple_rw="{ item }" >
                <v-checkbox v-model="item.supports_multiple_rw" @click="updateDevice(item)"></v-checkbox>
              </template>


              <template v-slot:item.address="{ item }">
                <v-edit-dialog :return-value.sync="item.address" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
                  <b>{{ item.address }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.address" single-line type='number' min="0" @input="numberChange()" :rules="[() => !isExistingvalue(item.address, 'address') || 'Address Existed']"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>

              <template v-slot:item.tcp_ip="{ item }">
                <v-edit-dialog :return-value.sync="item.tcp_ip" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
                  <b>{{ item.tcp_ip }}</b>
                  <template v-slot:input>
                    <v-form v-model="valid" ref="form" lazy-validation>
                      <v-text-field v-model="item.tcp_ip" single-line :rules="[() => !isExistingvalue(item.tcp_ip, 'tcp_ip') || 'IP Existed']"></v-text-field>
                    </v-form>
