              </template>

              <!-- <template v-slot:item.point_interval_ms_between_points="{ item }">
                <v-edit-dialog :return-value.sync="item.point_interval_ms_between_points" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
                  <b>{{ item.point_interval_ms_between_points }}</b>
