                  </template>
                </v-edit-dialog>
              </template> -->

              <template v-slot:header.enable="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.supports_multiple_rw="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('supports_multiple_rw') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('supports_multiple_rw')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.tcp_port="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('port') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('tcp_port')" color="primary">mdi-pencil</v-icon>
