              </template>

              <template v-slot:header.ping_point="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('ping_point') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('ping_point')" color="primary">mdi-pencil</v-icon>
