        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="network.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" label="device name" required :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.supports_multiple_rw" label="supports multiple r/w"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_ip" label="IP" :rules="[() => !isExistingvalue(editedItem.tcp_ip, 'tcp_ip') || 'IP Existed']"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_port" label="Port" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.zero_mode" label="zero mode"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                  <v-text-field v-model="editedItem.ping_point" label="ping point (Func:Addr:Len 1:1:1)"></v-text-field>
                </v-col>
                <!-- <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.point_interval_ms_between_points" label="delay between points (ms)"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.timeout" label="timeout" type="number"></v-text-field>
                </v-col> -->
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.address" label="address" type="number" :rules="[() => !isExistingvalue(editedItem.address, 'address') || 'Address Existed']" ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="deviceHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="{
                  showFirstLastPage: true,
                  firstIcon: 'mdi-chevron-double-left',
                  lastIcon: 'mdi-chevron-double-right',
                  prevIcon: 'mdi-chevron-left',
                  nextIcon: 'mdi-chevron-right',
                }"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>

                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.uuid, 'uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createDevice(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.name="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.address="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.address" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.address" type="number" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.address, 'address') || 'Address Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.tcp_ip="{ item }" v-if="network.type == 'TCP'">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.tcp_ip" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.tcp_ip" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.tcp_ip, 'tcp_ip') || 'IP Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.tcp_port="{ item }" v-if="network.type == 'TCP'">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.tcp_port" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.tcp_port" type="number" single-line style="width: 100px"></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" min="0" v-model.number="editAll[editField]"  :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey } from '../../utils/utils';
import MbPoints from '../Modbus/MbPoints';
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';
import MqttConsole from '../Mqtt/MqttConsole';
import MbRightSidebar from './MbRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';

export default {
  components: {
    'points-table': MbPoints,
    MqttConsole,
    MbRightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog
  },
  data: () => ({
    topic: 'rubix/points/debug',
    tab: 0,
    valid: false,
    dialog: false,
    dialogDelete: false,
    deviceHeaders: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Name', value: 'name', align: 'start' },
      { text: 'Fault', value: 'fault', sortable: false },
      { text: 'Type', value: 'type', sortable: false },
      { text: 'Enable', value: 'enable', sortable: false },
      { text: 'Supports multiple r/w', value: 'supports_multiple_rw', sortable: false },
      { text: 'Address', value: 'address', sortable: false },
      { text: 'IP', value: 'tcp_ip', sortable: false },
      { text: 'Port', value: 'tcp_port', sortable: false },
      { text: 'Ping Point', value: 'ping_point', sortable: false },
      // { text: 'Point Interval MS Between Points', value: 'point_interval_ms_between_points', sortable: false },
      // { text: 'Timeout', value: 'timeout', sortable: false },
      { text: 'UUID', value: 'uuid', align: 'start', sortable: false },
    ],
    editedIndex: -1,
    editedItem: {
      name: '',
      // type: 'RTU',
      enable: true,
      supports_multiple_rw: false,
      address: 0,
      tcp_ip: '',
      tcp_port: 502,
      ping_point: '',
      zero_mode: false,
      history_enable: false,
      network_uuid: '',
    },
    defaultItem: {
      name: '',
      // type: 'RTU',
      enable: true,
      supports_multiple_rw: false,
      address: 0,
      tcp_ip: '',
      tcp_port: 502,
      ping_point: '',
      zero_mode: false,
      history_enable: false,
      network_uuid: '',
    },
    currentItem: {},
    dataImport: '',
    rules: { required: value => !!value || 'Required.' },
    network: {},
    selected: [],
    action: '',
    objectKeys: Object.keys,
    objectValues: Object.values,
    importArray: [],
    dialogImport: false,
    e1: 1,
    allPoints: [],
    dialogImportExcel: false,
    search: '',
    searchWizard: '',
    dialogMassEdit: false,
    editAll: {
      enable: false,
      supports_multiple_rw: false,
      tcp_port: 0,
      ping_point: "",
      history_enable: false,
      // timeout: 0,
      // point_interval_ms_between_points: 0,
    },
    editField: "",
    intervalId: null,
    devicesIncludeChild: [],
    isClickSaveImport: false,
    dirs: [],
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Device' : 'Edit Device';
    },
    refreshTime() {
      const { setting } = this.$store.state;
      return setting.refreshTime;
    },
    enableAutoRefresh() {
      const { setting } = this.$store.state;
      return setting.enableAutoRefresh;
    },
    networksData () {
      const { mbNetworks } = this.$store.state;
      return mbNetworks;
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getData();
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
      next();
    });
  },
  mounted() {
    this.getData();
    //set auto refresh
    if(this.refreshTime &&  this.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getData();
        this.$toasted.show(`Automatically refresh every ${this.refreshTime}ms`, { type: 'info' });
      }, this.refreshTime);
    }
  },
  methods: {
    async getData() {
      this.network = await axios.get(`point-server/network/${this.$route.params.id}?with_children=true`);
      this.getDirs();
      this.editedItem.network_uuid = this.defaultItem.network_uuid = this.network.uuid;
      //change headers
      if(this.network.type == "RTU"){
        const indexIP = this.deviceHeaders.findIndex(h=>h.value=='tcp_ip');
        if(indexIP > -1){
          this.deviceHeaders.splice(indexIP, 1);
        }
        const indexPort = this.deviceHeaders.findIndex(h=>h.value=='tcp_port');
        if(indexPort > -1){
          this.deviceHeaders.splice(indexPort, 1);
        }
      }
      //update MbNetworksData
      this.updateMbNetworksData(this.network);
    },
    getDirs(){
      this.dirs = [{text: "NETWORKS", link: '/modbus/networks'}];
      this.dirs.push({text: `NET: ${this.network.name}`});
    },
    updateMbNetworksData(newNetwork){
      const indexNetWork = this.networksData.findIndex(nw=>nw.uuid == newNetwork.uuid);
      if(indexNetWork > -1){
        Object.assign(this.networksData[indexNetWork], newNetwork);
        this.$store.commit('changeMbNetworksData', this.networksData);
      }
    },
    async getDeviceIncludeChild(item) {
      const device = await axios.get(`point-server/device/${item.uuid}?with_children=true`);
      this.devicesIncludeChild.push(device);
    },
    onClickOpen(item) {
      this.$router.push({ path: `/modbus/devices/${item.uuid}/points` });
    },
    editItem(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updateDevice(this.editedItem);
        } else {
          this.createDevice(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.network.devices.indexOf(this.currentItem);
      this.network.devices.splice(this.editedIndex, 1);
      await axios.delete(`point-server/device/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    async clickImportJson() {
      this.devicesIncludeChild = [];
      this.dialogImport = true;
      const promises = [];
      for (const item of this.selected) {
        promises.push(this.getDeviceIncludeChild(item));
      }
      await Promise.all(promises);
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.devicesIncludeChild) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.network_uuid = this.network.uuid;
            i = this.getPointsArray(i);
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImport = true;
          try{
            const promises = [];
            for (const item of this.importArray) {
              item.network_uuid = this.network.uuid;  //update to the current network_uuid
              if(!item.uuid){
                promises.push(this.createDevice(item));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){}
        }
      }
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(device=>{
        delete device.uuid;
        return device;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      const devices = _cloneDeep(this.network.devices);
      if (this.editedIndex !== -1) {
        //when edit
        devices.splice(this.editedIndex, 1);
      }
      if (fieldName === 'address' || fieldName === 'tcp_port') {
        value = Number(value);
      }
      return devices.find(d => d[fieldName] == value) != null;
    },
    async clickJsonExport() {
      this.devicesIncludeChild = [];
      const promises = [];
      for (const item of this.selected) {
        promises.push(this.getDeviceIncludeChild(item));
      }
      await Promise.all(promises);
      const status = copyToClipboard(JSON.stringify(this.devicesIncludeChild));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = this.convertToExportData(array);
      if (type === 'csv') {
        return exportCSV('devices', data);
      }
      if (type === 'excel') {
        return exportExcel('devices', data);
      }
      if (type === 'json') {
        return exportJSON('devices', JSON.stringify(this.network.devices));
      }
    },
    convertToExportData(array) {
      const data = _cloneDeep(array).map(device => {
        _forIn(device, (value, key) => {
          if (key == 'points') {
            //get points
            device.points.forEach((point, indexP) => {
              _forIn(point, (point_value, point_key) => {
                if (point_key != 'point_store') {
                  const pointKey = `point[${indexP}].${point_key}`;
                  device = { ...device, [pointKey]: point_value };
                } else {
                  //point_store
                  _forIn(point.point_store, (point_store_value, point_store_key) => {
                    const pointStoreKey = `point[${indexP}].point_store.${point_store_key}`;
                    device = { ...device, [pointStoreKey]: point_store_value };
                  });
                  delete point.point_store;
                }
              });
            });
            delete device.points;
          }
        });
        return device;
      });
      return data;
    },
    getPointsArray(device) {
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key => {
        let keys = key.split('.');
        let pointKey = '';
        let pointIndex = null;
        if (keys.length > 1) {
          pointKey = keys[1];
          if (!pointKeys.includes(pointKey)) {
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split('[')[1].slice(0, -1));
          if ((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)) {
            indexs.push(pointIndex);
          }
        }
      });
      indexs.forEach(i => {
        let point = {};
        pointKeys.forEach(pk => {
          point = { ...point, [pk]: device[`point[${i}].${pk}`] };
        });
        points.push(point);
      });
      device.points = points;
      return device;
    },
    updateDevice(data) {
      axios.put(`point-server/device/${data.uuid}`, data).then(response => {
        const index = this.network.devices.findIndex(d => d.uuid == response.uuid);
        Object.assign(this.network.devices[index], response);
        this.close();
        return this.$toasted.show('update success');
      });
    },
    async createDevice(data) {
      if(this.network.type == 'RTU'){
        delete data.tcp_ip;
        delete data.tcp_port;
      }
      data.type = this.network.type;
      await axios.post(`point-server/device`, data).then(response => {
        this.network.devices.push(response);
        if (this.importArray && this.importArray.length > 0) {
          const index = this.importArray.findIndex(n => n.name == response.name);
          if(index > -1){
            Object.assign(this.importArray[index], response);
          }
        }
        this.close();
        this.$toasted.show('create success');
        if (data.points && data.points.length > 0) {
          for (const point of data.points) {
            point.device_uuid = response.uuid;
            point.register = point.register ? point.register : 0;
            point.register_length = point.register_length ? point.register_length : 0;
            point.enable = point.enable ? point.enable : false;
            this.createPoint(point);
          }
        }
      });
    },
    async createPoint(data) {
      axios.post(`point-server/point`, data).then(response => {
      });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      this.editAll = {
        enable: false,
        supports_multiple_rw: false,
        tcp_port: 0,
        ping_point: "",
        history_enable: false,
        // timeout: 0,
        // point_interval_ms_between_points: 0,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    onClickRefresh(){
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    }


  },
};
</script>
